@import url("https://fonts.googleapis.com/css2?family=Just+Me+Again+Down+Here&display=swap");

.header-line1 {
  font-family: "Just Me Again Down Here";
  font-size: 3rem;
  line-height: 0px;
}

.header-line2 {
  font-family: "Just Me Again Down Here";
  font-weight: bolder;
  font-size: 4rem;
  line-height: 0px;
}

.header-line3 {
  font-family: "Just Me Again Down Here";
  font-size: 2rem;
  line-height: 0px;
}

.App {
  text-align: center;
  width: 100%;
  height: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.flower-and-header {
  display: flex;
  width: 100%;
  flex-direction: row;
}

.img-flower {
  position: absolute;
  width: 22%;
}

.HeaderBox {
  display: flex;
  background-color: #f5ece5;
  width: 100%;
  align-items: center;
  justify-content: center;
  align-self: center;
  margin-top: 3%;
}

.Header {
  width: 60%;
  display: flex;
  flex-direction: column;
}

.HeaderButtons {
  display: flex;
  flex-direction: row;
  width: 100%;
  align-items: center;
  justify-content: center;
  align-self: center;
}

.Line {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #ccc;
  margin: 1em 0;
  padding: 0;
  margin-top: 10%;
}

.MainPage {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #f5ece5;
}

.PageBox {
  margin-top: 3%;
  display: flex;
  align-self: center;
  justify-content: center;
  align-items: center;
  background-color: #f5ece5;
}

.flip-card {
  background-color: transparent;
  width: 300px;
  height: 400px;
  perspective: 1000px; /* Remove this if you don't want the 3D effect */
}

.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}

.flip-card:hover .flip-card-inner {
  transform: rotateY(180deg);
}

.flip-card-front,
.flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden; /* Safari */
  backface-visibility: hidden;
  display: grid;
  grid-auto-flow: row;
  border-style: solid;
  border-color: #ff00ff;
  border-width: 4px;
}

.flip-card-front {
  background-color: #a3b19b;
  color: whitesmoke;
}

.flip-card-back {
  background-color: rgb(93, 153, 187);
  color: whitesmoke;

  transform: rotateY(180deg);
}

.TextGroup {
  line-height: 0.8;
}

.MenuButton {
  flex: 1;
  background-color: #ffffff;
  font-size: medium;
  font-family: serif;
  border-width: 0px;
  background-color: #f5ece5;
}

.MenuButton-selected {
  flex: 1;
  background-color: #ffffff;
  font-size: medium;
  font-family: serif;
  border-width: 0px;
  font-weight: bolder;
}

.MenuButton:hover {
  background-color: #f5f5f5;
  cursor: pointer;
}

.welcome-page {
  width: 50%;
  height: 60%;
}

.notice-form {
  border-width: 3px;
  border-style: solid;
  border-color: #ccc;
  display: flex;
  flex-direction: column;
  margin-bottom: 1rem;
  align-items: center;
  width: 40%;
  background-color: #f7e7db;
}

.form-section {
  border-width: 3px;
  border-style: solid;
  border-color: #ccc;
  display: flex;
  flex-direction: column;
  margin-top: 1rem;

  margin-bottom: 1rem;
  align-items: flex-start;
  width: 90%;
  background-color: #fdf4ed;
}

.InputWithLabel {
  display: flex;
  flex-direction: column;
  margin: 1rem;
  align-items: flex-start;
}

.form-header {
  font-weight: bolder;
  font-size: 1.5rem;
  line-height: 1.2;
  padding-left: 1rem;
}

.form-radiobuttons-section {
  width: 100%;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.radiobuttons-box {
  border-width: 1px;
  border-style: solid;
  border-color: #ccc;
  border-bottom: none;
  width: 100%;
}

.form-radiobuttons {
  margin: 1rem;
  display: flex;
  align-items: flex-start;
}
